@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    & {
        position: relative;
        width: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        height: 100px;
        bottom: 0;
        left: 0;
        position: absolute;
        
    }
    .redes {
        & {
            width: 100%;
            display: flex;
            justify-content: space-around;
        }
        @include m.tablet {
            width: 30%;
        }

        a {
            padding: 15px 12px;
            border-radius: 5px 5px 50px 50px;
            font-size: 2rem;
            background-color: lighten(v.$negro, 15%);
            color: v.$blanco;
            transition: all .2s ease-in-out;
            box-shadow: 0 0 55px rgba(0, 0, 0, 0.39);
            &:hover {
                box-shadow: 5px 10px 5px rgba(0, 0, 0, 0.575);
                background-color: v.$negro;
                transform: translateY(-10px);
            }
        }
    }
}